.navbar {
    background: rgba(255, 255, 255, 0.8);  /* linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%) */
    backdrop-filter: blur(10px);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    position: sticky;
    top: 0;
    z-index: 10;
    border-bottom: 1px solid rgba(0,0,0,0.1);
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
    
  }
  
  .navbar-logo {
    color: #1d1d1f;  /* #fff */
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    /* font-family: 'Montez';  */
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
    letter-spacing: -0.5px;
    font-size: 1.4rem;
    font-weight: 600;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem; 
    gap: 5px; /* Adds space between items */
  }
  
  .nav-item {
    height: 80px;
    

    
  }
  
  .nav-links, .nav-links-contact {
    color: #1d1d1f; /* #fff */
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1.2rem;
    height: 100%;
    opacity: 0.8; /* Subtle text looks more high-end */
    font-weight: 400;
    font-family: 'Inter', -apple-system, sans-serif;
    /* transition: opacity 0.3s ease; */
  }


  
  .nav-links:hover {
    /*  border-bottom: 2px solid #242424; /* #fff 
    transition: all 0.2s ease-out; */
    opacity: 1; /* Brightens on hover instead of just adding a border */
    border-bottom: none; /* Modern navs often skip the bottom border */
  }

  .nav-links-contact:hover {
    color: #fff;
  }
  
  .fa-bars {
    color: #242424; /* #fff */
  }
  
  .nav-links-mobile {
    display: none;
    
    
  }
  
  .menu-icon {
    display: none;
  }

  
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #fff; /* #242222 */
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }


  
    .navbar-logo {
      position: absolute;
      top: 5px;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #000000;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #242424; /* #fff */
      padding: 14px 20px;
      border: 1px solid #242424; /* #fff */
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #242424; /* #fff */
      color: #fff; /* #fff */
      transition: 250ms;
    }
  }


  @media screen and (max-width: 400px){
    .navbar-logo {
      margin-left: 0px;
      font-size: 1.8rem;
      top : 10px;
    }

  }







